import React, { useState } from "react";
import Select from "react-select";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import CloseIcon from "@mui/icons-material/Close";

import {
  SortingState,
  IntegratedSorting,
  SearchState,
  IntegratedFiltering
} from "@devexpress/dx-react-grid";
import {
  Grid,
  Table,
  Toolbar,
  SearchPanel,
  TableHeaderRow
} from "@devexpress/dx-react-grid-material-ui";

import "bootstrap/dist/css/bootstrap.min.css";

export default function IncidentManager(props) {
  const dataSiteUrl = "/csb-cqrdw/data/";

  const [companies, setCompanies] = useState([]);
  const [companyID, setCompanyID] = useState(null);
  const [group, setGroup] = useState(null);
  const [groupID, setGroupID] = useState(null);
  const [org, setOrg] = useState(null);
  const [serviceIDs, setServiceIDs] = useState([]);
  const [groups, setGroups] = useState([]);
  const [services, setServices] = useState([]);
  const priorities = [
    { label: "Low", value: "0" },
    { label: "Medium", value: "1" },
    { label: "High", value: "2" }
  ];
  const [priorityIDs, setPriorityIDs] = useState([
    { label: "Low", value: "0" },
    { label: "Medium", value: "1" },
    { label: "High", value: "2" }
  ]);
  const [show24HrView, setShow24HrView] = useState(false);
  const [showAgeKpi, setShowAgeKpi] = useState(false);
  const [ageKpiBtnCss, setAgeKpiBtnCss] = useState("disabled");
  const [ageKpiArrowColor, setAgeKpiArrowColor] = useState("disabled");
  const [ageKpiOptionsCss, setAgeKpiOptionsCss] = useState("disabled");
  const [kpiDateField, setKpiDateField] = useState("Submit_Date");
  const [GreenMaxAge, setGreenMaxAge] = useState(90);
  const [YellowMaxAge, setYellowMaxAge] = useState(180);
  const [RedMaxAge, setRedMaxAge] = useState(240);

  const dateFields = [
    { label: "Submit Date", value: "Submit_Date" },
    { label: "Last Modified", value: "Last_Modified_Date" },
    { label: "Last Resolved", value: "Last_Resolved_Date" },
    { label: "Reported Date", value: "Reported_Date" },
    { label: "Responded Date", value: "Responded_Date" },
    { label: "Closed Date", value: "Closed_Date" },
    { label: "Event Date", value: "Event_Date" },
    { label: "Required Resolution", value: "Required_Resolution_DateTime" },
    { label: "Estimated Resolution", value: "Estimated_Resolution_Date" },
    { label: "Acknowledgement Date", value: "Acknowledgment_Start_Date" },
    { label: "Resolution Start Date", value: "Resolution_Start_Date" },
    { label: "Last SLA Hold Date", value: "Last_SLA_Hold_Date" },
    { label: "VIP Assignment Date", value: "VIPAssignmentDateTime" },
    { label: "VIP Dispatch Date", value: "VIPDispatchDateTime" }
  ];

  const columns = [
    { name: "ID", title: "ID" },
    { name: "Name", title: "Name" },
    { name: "Country", title: "Country" },
    { name: "House", title: "House" },
    { name: "Reign", title: "Reign" }
  ];

  const [rows, setRows] = useState([]);

  React.useEffect(() => {
    console.log(companies);

    if (companyID) {
      getGroups();
    }
    if (groupID) {
      getServices();
    }

    if (companyID) {
      getRows();
    }
  }, [companyID, groupID, serviceIDs, priorityIDs]);

  React.useEffect(() => {
    getCompanies();
  }, []);

  const getCompanies = () => {
    fetch(`${dataSiteUrl}companies.json`, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      }
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (x) {
        setCompanies(x);
      });
  };

  const getGroups = () => {
    fetch(`${dataSiteUrl}groups.json?CompanyID=${companyID}`, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      }
    })
      .then(function (response) {
        //console.log(response);
        return response.json();
      })
      .then(function (myJson) {
        //console.log(myJson);
        setGroups(myJson);
      });
  };

  const getRows = () => {
    console.log("fetch");
    fetch(
      `${dataSiteUrl}incidents.json?CompanyID=${companyID}&Org=${org}&GroupID=${groupID}&Priorities=${priorities}&Services=${services}`,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json"
        }
      }
    )
      .then(function (response) {
        //console.log(response);
        return response.json();
      })
      .then(function (myJson) {
        //console.log(myJson);
        setRows(myJson);
      });
  };

  const getServices = () => {
    fetch(
      `${dataSiteUrl}services.json?CompanyID=${companyID}&Org=${org}&GroupID=${groupID}&Priorities=${priorities}`,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json"
        }
      }
    )
      .then(function (response) {
        return response.json();
      })
      .then(function (myJson) {
        setServices(myJson);
      });
  };

  const onCompanyChange = (val) => {
    setGroupID(null);
    setServiceIDs([]);
    setCompanyID(val.value);
  };

  const onGroupChange = (val) => {
    setGroup(val);
    if (val.indexOf("|") !== -1) {
      setOrg(val.split("|")[0]);
      setGroupID(val.split("|")[1]);
    }
  };

  const onShowAgeKpiChange = (val) => {
    setShowAgeKpi(!showAgeKpi);
    if (!showAgeKpi) {
      setAgeKpiArrowColor("primary");
      setAgeKpiBtnCss("active");
    } else {
      setAgeKpiArrowColor("disabled");
      setAgeKpiBtnCss("disabled");
      setAgeKpiOptionsCss("disabled");
    }
  };

  const onShowAgeKpiClick = () => {
    if (showAgeKpi) {
      setAgeKpiOptionsCss(
        ageKpiOptionsCss === "enabled" ? "disabled" : "enabled"
      );
      setAgeKpiBtnCss(ageKpiOptionsCss === "enabled" ? "active" : "selected");
    }
  };

  return (
    <div>
      <div class="filter-container">
        <form>
          <label class="label-nowrap">
            Company:
            <Select
              onChange={onCompanyChange}
              options={companies}
              value={companies.filter(function (option) {
                return option.value === companyID;
              })}
            ></Select>
          </label>
          <label>
            Group:
            <Select
              value={groups.filter(function (option) {
                return option.value === group;
              })}
              onChange={onGroupChange}
              options={groups}
              getOptionLabel={(x) => x.label}
              getOptionValue={(x) => x.value}
            ></Select>
          </label>
          <label>
            Services:
            <Select
              value={services.filter(function (service) {
                return serviceIDs.some((x) => service === x);
              })}
              onChange={(val) =>
                setServiceIDs((serviceIDs) => {
                  return val;
                })
              }
              options={services}
              isMulti
            ></Select>
          </label>

          <label>
            Priorities:
            <Select
              value={priorities.filter(function (priority) {
                return priorityIDs.some((x) => priority.value === x.value);
              })}
              onChange={(val) =>
                setPriorityIDs((priorityIDs) => {
                  return val;
                })
              }
              options={priorities}
              class="form-check-input"
              isMulti
            ></Select>
          </label>
          <div style={{ paddingTop: 10 }}>
            <label>
              <div class="form-check form-switch">
                <input
                  class="form-check-input"
                  type="checkbox"
                  defaultChecked={show24HrView}
                  onChange={() => setShow24HrView(!show24HrView)}
                />
                <label class="form-check-label" for="flexSwitchCheckDefault">
                  Show 24hr view
                </label>
              </div>
            </label>

            <label>
              <div class="form-check form-switch">
                <input
                  class="form-check-input"
                  type="checkbox"
                  defaultChecked={showAgeKpi}
                  onChange={onShowAgeKpiChange}
                />
                <label
                  onClick={onShowAgeKpiClick}
                  class={"form-check-label age-kpi-btn-" + ageKpiBtnCss}
                  for="flexSwitchCheckDefault"
                >
                  Show age indicators
                  <ArrowDropDownIcon color={ageKpiArrowColor} />
                  <div
                    onClick={(event) => event.stopPropagation()}
                    class={"age-kpi-options-" + ageKpiOptionsCss}
                  >
                    <span class="close-icon" onClick={onShowAgeKpiClick}>
                      <CloseIcon fontSize="small" />
                    </span>
                    <div class="label-nowrap">
                      Age field:&nbsp;
                      <label>
                        <Select
                          onChange={(val) => setKpiDateField(val.value)}
                          options={dateFields}
                          value={dateFields.filter(function (option) {
                            return option.value === kpiDateField;
                          })}
                        ></Select>
                      </label>
                      <br />
                      Green Max Age:&nbsp;
                      <label>
                        <input
                          value={GreenMaxAge}
                          onChange={(tf) => setGreenMaxAge(tf.value)}
                          type="text"
                          class="form-control"
                        />
                      </label>
                      <br />
                      Yellow Max Age:&nbsp;
                      <label>
                        <input
                          value={YellowMaxAge}
                          onChange={(tf) => setYellowMaxAge(tf.value)}
                          type="text"
                          class="form-control"
                        />
                      </label>
                      <br />
                      Red Max Age:&nbsp;
                      <label>
                        <input
                          value={RedMaxAge}
                          onChange={(tf) => setRedMaxAge(tf.value)}
                          type="text"
                          class="form-control"
                        />
                      </label>
                    </div>
                    <span class="footer">* in minutes</span>
                  </div>
                </label>
              </div>
            </label>
          </div>
        </form>
      </div>
      <div class="grid">
        <Grid rows={rows} columns={columns}>
          <SortingState
            defaultSorting={[{ columnName: "Name", direction: "asc" }]}
          />
          <IntegratedSorting />
          <SearchState defaultValue="" />
          <IntegratedFiltering />

          <Table />
          <TableHeaderRow showSortingControls />
        </Grid>
      </div>
    </div>
  );
}
